<template>
  <div class="main dataSummary quality-info">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">开始日期</span>
        <span class="item-right">
          <el-date-picker
            v-model="search.beginCalldate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            @change="getSbeginTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">结束日期</span>
        <span class="item-right">
            <el-date-picker
              v-model="search.endCalldate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
              :picker-options="endDatePicker"
              @change="getSendTime">
         </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">外呼团队</span>
        <span class="item-right">
          <el-select v-model="search.corp" multiple collapse-tags @change="changeStaff('corp')">
              <template v-for="(item, key) in teamData">
                  <el-option :label="item" :value="key"  :key="key">{{item}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select v-model="search.prjid_local" multiple collapse-tags @change="changeStaff('proj')">
              <template v-for="(item, key) in projData">
                  <el-option :label="item" :value="key"  :key="key">{{item}}</el-option>
              </template>
           </el-select>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">坐席姓名</span>
        <span class="item-right">
          <el-input placeholder="点击选择" readonly style="cursor: pointer;" v-model="search.uname" @click.native="dialog_batch"></el-input>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_data">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
      </div>
      <el-table
        :data="list.data"
        style="width: 100%"
        border
        @sort-change='sortChange'
        :cell-style="cellStyle"
      >
        <el-table-column
          prop="dateline"
          align="center"
          width="100"
          :formatter="formatDate"
          label="日期"
        >
        </el-table-column>
        <el-table-column
          prop="cid"
          align="center"
          label="外呼团队"
        >
        <template slot-scope="scope">
            <p type="text" size="small" v-if='scope.row.cid!=undefined'>{{teamData[scope.row.cid]}}</p>
            <p type="text" size="small" v-else>--</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="prjid_local"
          align="center"
          label="品牌"
          width="90">
          <template slot-scope="scope">
            <p type="text" size="small" v-if='scope.row.prjid_local!=undefined'>{{projData[scope.row.prjid_local]}}</p>
            <p type="text" size="small" v-else>--</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="uname"
          align="center"
          label="坐席工号"
          
        >
        </el-table-column>
        <el-table-column
          prop="umemo"
          align="center"
          label="坐席姓名"
        >
        </el-table-column>
        <el-table-column
          prop="connect"
          align="center"
          label="接通数"
        >
        </el-table-column>
        <el-table-column
          prop="duration"
          align="center"
          label="通话量"
        >
        </el-table-column>
        <el-table-column
          prop="leads"
          align="center"
          label="成功数"
          width="90"
          sortable='custom'
        >
        </el-table-column>
        <el-table-column
          prop="leadsRate"
          align="center"
          width="90"
          label="成功率"
          sortable='custom'
        >
          <template slot-scope="scope">
            <p type="text" size="small">{{scope.row.leadsRate}}%</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="statusNum"
          align="center"
          label="不合格数"
        >
        </el-table-column>
        <el-table-column
          prop="detention"
          align="center"
          label="挽留能力"
          width="120"
          sortable='custom'
        >
          <template slot-scope="scope">
            <p type="text" size="small">{{scope.row.detention}}%</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="close_order"
          align="center"
          label="关单能力"
          sortable='custom'
          width="120"
        >
          <template slot-scope="scope">
            <p type="text" size="small">{{scope.row.close_order}}%</p>

          </template>
        </el-table-column>
        <el-table-column
          prop="payWorktime"
          align="center"
          label="实际工作时长"
          width="120"
        >
        </el-table-column>
      </el-table>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog :visible.sync="popup_show">
      <popup-box :dataList='userData' ref="getDataName"></popup-box>
      <div slot="footer" class="dialog-footer">
        <el-button @click="popup_show=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {format} from '@/config/util'
  import pageTable from '@/components/pager'
  import popupBox from '@/components/popup'
  import ajax from '@/config/ajax'
  import Qs from 'qs'
  export default {
    components:{pageTable, popupBox},
    data(){
      return{                   //数据集
        search:{
          corp:[],       //外呼团队
          prjid_local: [],       //品牌
          uname: '' ,      //坐席姓名
          uservalue: '0',   //坐席id
          beginCalldate: new Date(),       //开始时间
          endCalldate: new Date(),         //结束时间
          orderbyCondition: 'leads', //排序条件
          orderbyFlag: false,  //是否倒叙
          size:10,            //每页数量
          page:1              //页码
        },
        list:{
          data:[],            //数据集
          totalPage:0,        //总页数
          totalCount:0        //总条数
        },
        total: {
          sumduration: '',  //总通话时长
          sumconnect: '',    //总接通数
          sumleads: '',    //总成功数
          sumleadsrate: '',   //总成功率
          sumdetention: '',    //总挽留能力
          sumcloseorder: '',   //总关单能力
          sumpaywork: '',    //总工作时长
        },
        popup_show:false,
        endDatePicker: this.processDate(),  //结束时间不能大于开始时间
        teamData: [],         //外呼团队字典数组
        projData: [],         //品牌字典数组
        userData: [],         //坐席姓名字典数组
      }
    },
    created() {
      this.teamData = JSON.parse(localStorage.getItem("brand"));
      this.projData = JSON.parse(localStorage.getItem("pro"));
      this.userData = JSON.parse(localStorage.getItem("user"));
      this.search.corp[0] = '0';
      this.search.prjid_local[0] = '0';
      this.getInitialList();
    },
    methods:{
      // 查询
      search_data () {
        if(this.search.beginCalldate==null){
          this.$message({
            message: '请选择开始时间',
            type: 'warning'
          });
          return
        }
        if(this.search.endCalldate == null){
          this.$message({
            message: '请选择结束时间',
            type: 'warning'
          });
          return
        }
        if(this.search.beginCalldate !=null && this.search.endCalldate !=null){
          if(this.search.beginCalldate.getTime() > this.search.endCalldate.getTime()){
            this.$message({
              message: '开始日期不能大于结束日期',
              type: 'warning'
            });
            return
          }
        }
        if(this.search.corp.length == 0){
          this.$message({
            message: '请选择外呼团队',
            type: 'warning'
          });
          return
        }
        if(this.search.prjid_local.length == 0){
          this.$message({
            message: '请选择品牌',
            type: 'warning'
          });
          return
        }
        this.search.page = 1
        this.getInitialList()
      },
      processDate(){
        const  self = this;
        return {
          disabledDate(time) {
            if (self.search.beginCalldate) {  //如果开始时间不为空，则结束时间大于开始时间
              return new Date(self.search.beginCalldate).getTime()- 8.64e7 > time.getTime()
            } else {
              // return time.getTime() > Date.now()//开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      changeStaff(type){
        if(type == 'corp'){
          if(this.isCorpContainAll){
            this.isCorpContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.corp.splice(0, 1);// 则删除第一个全部
          }else{
            this.isCorpContainAll = this.search.corp.some(value => value === 0)
            if(this.isCorpContainAll){
              this.search.corp = [0];
            }else{
              if(this.search.corp.length == this.teamData.length-1){
                this.search.corp = [0];
                this.isCorpContainAll = true;
              }
            }
          }
        }else if(type == 'proj'){
          if(this.isProjContainAll){
            this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
            this.search.prjid_local.splice(0, 1);// 则删除第一个全部
          }else{
            this.isProjContainAll = this.search.prjid_local.some(value => value === 0)
            if(this.isProjContainAll){
              this.search.prjid_local = [0];
            }else{
              if(this.search.prjid_local.length == this.projData.length-1){
                this.search.prjid_local = [0];
                this.isProjContainAll = true;
              }
            }
          }
        }
      },
      //排序
      sortChange(column){
        console.log(column.order)
        this.search.orderbyCondition = column.prop;
        if(column.order == 'ascending'){
          this.search.orderbyFlag = true;
        }else if(column.order == 'descending'){
          
          this.search.orderbyFlag = '';
        }
        this.getInitialList();
      },
      //请求获取数据
      getInitialList(){
        var params = {
          beginCalldate:format(this.search.beginCalldate, "yyyyMMdd"),
          endCalldate:format(this.search.endCalldate, "yyyyMMdd"),
          corp: this.search.corp.join(","),
          prjid_local: this.search.prjid_local.join(","),
          uname: this.search.uservalue,
          orderbyCondition:this.search.orderbyCondition,
          orderbyFlag:this.search.orderbyFlag,
          page:this.search.page,
          size:this.search.size
        }
        return ajax.request({
          method:'post',
          url:'seat',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
            params = Qs.stringify(params)
            return params
          }],
          data:params
        }).then((res)=>{
          this.list.data=res.data.list;
          this.list.totalCount=res.data.totalCount;
          this.list.totalPage=res.data.totalPage;
          let obj = {
            dateline: '--',
            crop: '--',
            porj: '--',
            uname: '--',
            umemo: '--',
            connect: res.data.sumconnect,
            duration: res.data.sumduration,
            connectRate: res.data.sumconnectrate,
            leads: res.data.sumleads,
            leadsRate: res.data.sumleadsrate,
            statusNum: '--',
            detention: res.data.sumdetention,
            close_order: res.data.sumcloseorder,
            payWorktime: res.data.sumpaywork
          };
          this.list.data.unshift(obj);
          this.total.sumduration = res.data.sumduration;
          this.total.sumconnect = res.data.sumconnect;
          this.total.sumleads = res.data.sumleads;
          this.total.sumleadsrate = res.data.sumleadsrate;
          this.total.sumdetention = res.data.sumdetention;
          this.total.sumcloseorder = res.data.sumcloseorder;
          this.total.sumpaywork = res.data.sumpaywork;
        }).catch((error)=>{
          console.log(error)
        })
      },
      //格式化日期
      formatDate(row){
        var calldate = row.dateline.toString();
        return row.dateline = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
      },
      //合计行设置样式
      cellStyle({rowIndex}){
        if(rowIndex == '0'){
          return "color:red"
        }
      },
      //处理外乎团队的数据
      formatCorp(row){
        for(var i=0;i<this.teamData.length;i++){
          if(row.cid == this.teamData[i].id){
            return row.crop = this.teamData[i].memo;
          }
        }
      },
      //处理坐席姓名数据
      formatUser(row){
        for(var i=0;i<this.userData.length;i++){
          if(row.umemo == this.userData[i].id){
            return row.uid = this.userData[i].memo;
          }
        }
      },
      //处理品牌的数据
      formatProj(row){
        var projdata = this.projData;
        projdata = projdata.slice(1);
        for(var i=0;i<projdata.length;i++){
          if(row.prjid_local == projdata[i].id){
            return row.porj = projdata[i].memo;
          }
        }
      },
      //时间选择器开始时间
      getSbeginTime(val){
        this.search.beginCalldate=new Date(val);
      },
      //时间选择器结束时间
      getSendTime(val){
        this.search.endCalldate=new Date(val);
      },
      dialog_batch(){
        this.dataType = this.userData;
        this.dataId = '4'
        this.popup_show = true;
      },
      //坐席姓名弹框点击确认获取信息
      save() {
        console.log("111111");
        this.popup_show = false;
        if(this.$refs.getDataName.complete()[0] != ''){
          this.search.uname = this.$refs.getDataName.complete()[0].join(",");
          this.search.uservalue = this.$refs.getDataName.complete()[1].join(",");
        }else{
          this.search.uname = '';
          this.search.uservalue = '0';
        }
      },
      //合计
      getTotal(param){
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          const values = data.map(item => Number(item[column.property]));
          if(column.property === 'duration'){
            sums[index] = this.total.sumduration;
          }else if(column.property === 'connect') {
            sums[index] = this.total.sumconnect;
          }else if(column.property === 'leads') {
            sums[index] = this.total.sumleads;
          }else if(column.property === 'leadsRate') {
            sums[index] = this.total.sumleadsrate+"%";
          }else if(column.property === 'detention') {
            sums[index] = this.total.sumdetention+"%";
          }else if(column.property === 'close_order') {
            sums[index] = this.total.sumcloseorder+"%";
          }else if(column.property === 'payWorktime') {
            sums[index] = this.total.sumpaywork;
          }else{
            sums[index] = '--';
          }
        })
        return sums;
      },
      //分页
      change_data(index,size){
        this.search.page=index;
        this.search.size=size;
        this.getInitialList();
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/pages/main.css";
  .quality-info{
    padding-left: 15px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }
  .dataSummary .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width:150px;
  }
  select.txt-common{
    height:40px;
  }
  .dataSummary .main-search .search-item{
    min-width: 100px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }

  .dataSummary .main-table table tbody tr td .quality,
  .dataSummary .main-table table tbody tr td .qualified,
  .dataSummary .main-table table tbody tr td .unqualified{
    display: inline-block;
    width: 80px;
    height: 24px;
    background-color: #c2c2c2;
    color: #ffffff;
    font-size: 12px;
    line-height: 24px;
    text-align: center;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  .dataSummary .main-table table tbody tr td .qualified{
    background-color: #2cdb6e;
  }
  .dataSummary .main-table table tbody tr td .unqualified{
    background-color: #de2f2f;
  }

  .dataSummary .main-table table tbody tr a.disabled{
    color: #b4b4b4;
    cursor:no-drop;
  }

  .dataSummary .edit-cross{
    width: 80%;
    text-align: center;
    margin: 0 auto;
  }

  .dataSummary .file-upload .edit-content{
    height: 300px;
  }

  .dataSummary .file-upload .sub-content{
    padding: 10px 20px;
    border:1px solid #ff6825;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    margin-bottom: 15px;
    color: #ff6825;
    font-size: 12px;
    line-height: 24px;
  }
  .dataSummary .file-upload .sub-content p{
    margin: 0;
    padding: 0;
  }
  .dataSummary .file-upload .sub-content p span{
    font-weight: 600;
    margin-right: 5px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }

</style>



